










































































.oslo__card-image {
  background-blend-mode: multiply;
  color: white;
  cursor: pointer;
}

.oslo__category {
  padding: 0.25rem;
  color: rgb(41, 40, 88);
  font-weight: bold;
}

.oslo__tema {
  font-size: 24px;
  font-weight: bold;
  text-shadow: 0 1px 3px rgba(0, 0, 0, 0.78);
}

.oslo__see-more-text {
  position: absolute;
  bottom: 0.2rem;
  font-weight: bold;
  text-shadow: 0 1px 3px rgba(0, 0, 0, 0.78);
}
